.addNewTradeForm {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(5, auto);
    gap: 1em;
}


/* Assign elements to specific grid rows */

.addNewTradeForm>div:nth-child(1) {
    grid-row: 1;
}

.addNewTradeForm>div:nth-child(2) {
    grid-row: 1;
}

.addNewTradeForm>div:nth-child(3) {
    grid-row: 2;
}

.addNewTradeForm>div:nth-child(4) {
    grid-row: 2;
}

.addNewTradeForm>div:nth-child(5) {
    grid-row: 3;
}

.addNewTradeForm>div:nth-child(6) {
    grid-row: 3;
}

.addNewTradeForm>div:nth-child(7) {
    grid-row: 4;
}

.addNewTradeForm>div:nth-child(8) {
    grid-row: 4;
}


/* Make the date input span both columns */

#dateInput {
    grid-column: 1 / span 2;
    grid-row: 5;
}